.SA_flex-grid {
    max-width: 1490px;
    margin: 0 auto;
    padding: 0 #{($margins) +"px"};
    display: block;
    justify-content: space-around;
    margin-bottom: #{($margins) +"px"};
    // gap:  #{($margins) +"px"};

    @include tablet {
        margin-bottom: #{($margins) +"px"};
        // display: -ms-flexbox;
        display: flex;
    }

    @include desktop {

        // display: -ms-flexbox;
        display: flex;
    }
}


.SA_hide-mobile {
    @include mobile-only {
        display: none !important;
    }
}

.SA_hide-tablet {
    @include tablet-only {
        display: none !important;

    }
}

.SA_hide-desktop {
    @include desktop-only {
        display: none !important;
    }

    @include xl-desktop-only {
        display: none !important;
    }
}

// class to add to flex items to prevent them from stacking on mobile
.SA_flex-grid--no-stack {
    display: -ms-flexbox;
    display: flex;
}

.SA_text-grid {
    max-width: 1490px;
    margin: 0 auto;
    position: relative;
    z-index: 1;
    padding: 0 #{($margins) +"px"};
}

