body {
  overscroll-behavior-y: none;
}

#SA_main-body video {
  width: 100%;
}

#SA_main-body *,
#SA_main-body :after,
#SA_main-body :before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

#SA_main-body img {
  max-width: 100%;
  width: 100%;
  flex-shrink: 0;
  align-self: flex-start;
}

#SA_main-body,
#SA_editors-note {
  font: 16px Inter, serif;
  line-height: 1.58;
  letter-spacing: 0.01em;
  color: #a5a5a5;
  background: #faf9f7;
  text-rendering: geometricPrecision;
  -webkit-font-smoothing: antialiased;
  margin: 0;
}

#SA_main-body p {
  font: 16px Inter, serif;
  line-height: 1.58;
  letter-spacing: 0.01em;
  color: #a5a5a5;
  text-rendering: geometricPrecision;
  -webkit-font-smoothing: antialiased;
  margin-block-start: 0.5em;
  margin-block-end: 0.5em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}

@media (min-width: 701px) {
  #SA_main-body,
  #SA_editors-note,
  #SA_main-body p {
    font-size: 18px;
  }
}

@media (min-width: 1600px) {
  #SA_main-body,
  #SA_editors-note,
  #SA_main-body p {
    font-size: 18px;
  }
}

.SA_body--light {
  background: #f7f5f0;
}

.SA_body--dark {
  background: #080808;
}

.SA_h1,
.SA_h2,
.SA_h3,
.SA_h4,
.SA_h5 {
  margin: 2.75rem 0 1rem 0;
  font-weight: 400;
  line-height: 1.28;
  font-family: Inter, sans-serif;
  color: rgba(255, 255, 255, 0.92);
  font-weight: bold;
  letter-spacing: -0.02em;
}

.SA_h1 {
  font-size: 2.488rem;
}

.SA_h2 {
  font-size: 2.074rem;
}

.SA_h3 {
  font-size: 1.728rem;
}

.SA_h4 {
  font-size: 1.44rem;
}

.SA_h5 {
  font-size: 1.2rem;
}

.SA_p {
  margin: 1em 0;
}

.SA_subtitle {
  font-family: "Inter", sans-serif;
  font-size: 1.3em;
  font-weight: bold;
  color: rgba(255, 255, 255, 0.92);
  margin: 1.2em 0;
  text-transform: uppercase;
  max-width: 80%;
}

@media (min-width: 701px) {
  .SA_subtitle {
    max-width: 100%;
    text-align: left;
    font-size: 2em;
  }
}

.SA_subtitle + p {
  margin: -1em 0 2em 0;
}

.SA_text_small {
  font-size: 0.9rem;
}

#SA_main-body a,
#SA_editors-note a {
  color: #f15252;
}

#SA_main-body a:hover,
#SA_main-body a:active,
#SA_main-body a:focus,
#SA_editors-note a:hover,
#SA_editors-note a:active,
#SA_editors-note a:focus {
  color: #0066d6;
}

.SA_dropcap {
  font-size: 4.3333333333em;
  display: block;
  float: left;
  line-height: 0.58;
  margin-right: 7px;
  margin-top: -2px;
  margin-left: -4px;
  color: rgba(255, 255, 255, 0.92);
  font-family: 'Barlow Condensed', sans-serifs;
  font-weight: 700;
}

@media (min-width: 701px) {
  .SA_dropcap {
    line-height: 0.55;
  }
}

.SA_pullquote {
  position: relative;
  float: left;
  margin: 2px 0;
  font-family: "Inter", sans-serif;
  font-weight: bold;
  line-height: 1.28;
  padding: 1rem 0;
  word-break: break-word;
  font-style: italic;
  color: rgba(255, 255, 255, 0.92);
  text-transform: uppercase;
  hanging-punctuation: first;
  margin-top: -1rem;
  border: none;
  font-size: 1.5em;
  margin-top: -2rem;
  width: 100%;
  display: inline-block;
}

@media (min-width: 701px) {
  .SA_pullquote {
    width: 60%;
    padding-right: 0;
    margin-bottom: 0;
    border-bottom: none;
    font-weight: bold;
    font-size: 2rem;
    padding-right: 2rem;
  }
}

@media (min-width: 1600px) {
  .SA_pullquote {
    font-size: 42px;
  }
}

.SA_inset {
  width: 100%;
  float: left;
  margin-left: -57%;
  padding: 1rem 1rem 0 0;
}

.SA_inset img {
  width: 100%;
}

.SA_pullquote img,
.SA_pullquote--right img {
  width: 100%;
}

.SA_pullquote .SA_caption-left-flush {
  padding: 0.2rem 0 0 0;
  font-family: "Barlow Condensed", sans-serif;
  font-size: 1.1rem;
}

@media (min-width: 1025px) {
  .SA_pullquote span {
    margin: 0;
  }
  .SA_pullquote {
    display: block;
    font-size: 42px;
    margin-right: 1.4rem;
    width: calc((100% * 8/14) - 25px);
    margin-left: calc((-100% * 3/14) - 25px);
  }
  .SA_pullquote--right {
    padding-left: 0 !important;
    float: right;
    display: block;
    font-size: 42px;
    border: none;
    margin-left: 1rem;
    padding-left: 1.4rem;
    width: calc((100% * 8/14) - 25px);
    margin-right: calc((-100% * 2/14) - 25px);
  }
}

.SA_caption-right {
  font-size: 0.9rem;
  margin: -11px 0;
  text-align: left;
  line-height: 1.58;
  color: rgba(56, 56, 56, 0.83);
}

.SA_caption-right--subscribe {
  padding: 1rem;
  background-color: #0065a4;
  color: #fff;
}

.SA_caption-right .SA_caption-left-flush {
  padding: 0;
}

.SA_caption-right img {
  width: 100%;
}

@media (min-width: 1025px) {
  .SA_caption-right img {
    width: 50%;
  }
}

.SA_caption-credit {
  font-style: normal;
  color: rgba(123, 123, 123, 0.83);
}

@media (min-width: 1025px) {
  .SA_caption-right {
    padding-right: 0.9rem;
    /* text-align: right; */
    width: calc((100% * 2/14) - 22px);
    position: absolute;
    margin-left: calc((-100% * 2/14) - 22px);
    margin-top: 0.4rem;
  }
  .SA_caption-right--subscribe {
    padding: 0.9rem;
    border: none;
    width: calc((100% * 3/14) - 22px);
    margin-left: calc((-100% * 3/14) - 44px);
  }
  .SA_caption-right--large {
    width: calc((100% * 4/14) - 22px);
    margin-left: calc((-100% * 4/14) - 0px);
  }
  .SA_caption-right img {
    width: 100%;
  }
}

@media (min-width: 1600px) {
  .SA_caption-right--subscribe {
    width: calc((100% * 3/14) - 22px);
    margin-left: calc((-100% * 3/14) - 44px);
  }
  .SA_caption-right,
  .SA_caption-left-flush,
  .SA_caption-center {
    font-size: 0.9rem;
  }
}

.SA_caption-center,
.SA_caption-left-flush {
  background: transparent;
  font-size: .8rem;
  margin: 0 0 -7px;
  line-height: 1.48;
  color: rgba(56, 56, 56, 0.83);
  font-family: "Inter", sans-serif;
  width: calc(100vw - 2.8rem);
}

.SA_caption-center .SA_caption__description,
.SA_caption-left-flush .SA_caption__description {
  margin-right: 0.5rem;
  display: inline-block;
}

.SA_caption-center .SA_caption__credit,
.SA_caption-left-flush .SA_caption__credit {
  font-weight: 400;
  text-transform: uppercase;
  opacity: 0.6;
  font-size: .7rem;
  line-height: 2;
  display: inline-block;
}

@media (min-width: 701px) {
  .SA_caption-center,
  .SA_caption-left-flush {
    padding: 0;
    width: auto;
    max-width: 665px;
  }
}

@media (min-width: 701px) {
  .SA_caption--bottom-right {
    position: absolute;
    bottom: 0;
  }
}

@media (min-width: 701px) {
  .SA_caption-center {
    text-align: center;
  }
}

.SA_related-article {
  text-align: left;
  font-family: "Inter", sans-serif;
  font-style: normal;
  line-height: 1.3;
  color: rgba(56, 56, 56, 0.83);
  font-size: 15px;
}

.SA_related-article img {
  width: 41%;
  padding-right: 1%;
}

.SA_related-article p {
  margin: 0;
  float: right;
  width: 56%;
  font-size: 15px;
}

.SA_related-article a {
  color: rgba(56, 56, 56, 0.83);
  text-decoration: none !important;
  display: block;
}

.SA_related-article__heading {
  margin: 0 0 8px 0;
  font-weight: bold;
  font-style: normal;
  color: rgba(255, 255, 255, 0.92);
  font-size: 15px;
  font-family: "Inter", sans-serif;
  border-bottom: 2px solid rgba(0, 0, 0, 0.07);
  padding-bottom: 5px;
}

@media (min-width: 701px) {
  .SA_related-article a {
    display: inline;
  }
  .SA_related-article img {
    width: 100%;
    padding-right: 0;
  }
  .SA_related-article p {
    margin: 0;
    float: none;
    width: 100%;
    font-size: 15px;
  }
}

.SA_pullquote img {
  width: 100%;
}

#SA_main-body hr {
  border: none;
  border-top: 1px solid #e0d7c4;
  border-top: 25px solid rgba(0, 0, 0, 0.07);
  width: calc((100% * 8/14) - 25px);
  margin: 2em auto;
}

.SA_climate-change-logo {
  width: 50px;
  display: block;
  margin: 0 auto;
  margin-bottom: 1.4rem;
}

.SA_header {
  width: 100%;
  position: relative;
  margin-bottom: -95vh;
}

.SA_header-image {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: bottom;
  position: absolute;
  top: 0;
  z-index: -1;
  overflow: hidden;
}

.SA_header-image:after {
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, #faf9f7), url("../img/overlay.SA_png");
  height: 100%;
  width: 100%;
  content: '';
  display: block;
}

.SA_header-image--fade-out .SA_header-image {
  backface-visibility: hidden;
  transform: translateZ(0);
  width: 100%;
  background-size: cover;
  background-position: bottom;
  background-color: #000;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.SA_header .SA_header-text {
  text-rendering: geometricPrecision;
  -webkit-font-smoothing: antialiased;
  line-height: 1.3;
  margin: 0 auto;
  max-width: 700px;
  text-align: center;
  padding: 10vh 0 113vh 0;
  max-width: 75rem;
  font-size: 17vw;
  font-family: "Inter", sans-serif;
  text-transform: uppercase;
}

@media (min-width: 1025px) {
  .SA_header .SA_header-text {
    padding: 12vh 0 120vh 0;
    font-size: 11rem;
  }
}

@media (min-width: 1600px) {
  .SA_header .SA_header-text {
    padding: 12vh 0 120vh 0;
    font-size: 13rem;
  }
}

@media screen and (max-height: 1080px) and (min-width: 600px) {
  .SA_header .SA_header-text {
    padding: 3vh 0 120vh 0;
  }
}

.SA_header h1 {
  letter-spacing: -0.015em;
  margin: 0;
  line-height: 0.8;
  font-size: 1em;
  color: rgba(255, 255, 255, 0.92);
  font-weight: 700;
  -webkit-mask: linear-gradient(to bottom, black 20%, rgba(0, 0, 0, 0.05) 105%);
}

.SA_header h1 span {
  display: block;
}

.SA_header h2,
.SA_header h3 {
  color: white;
  font-size: 0.3em;
  margin-top: 0.4em;
  opacity: 0.5;
}

@media (min-width: 701px) {
  .SA_header h2,
  .SA_header h3 {
    font-size: 0.13em;
    margin-top: 1em;
  }
}

.SA_section--light {
  background: #f7f5f0;
  color: rgba(0, 0, 0, 0.76);
}

.SA_section--light .SA_subtitle {
  color: rgba(0, 0, 0, 0.92);
}

.SA_section--light .SA_caption-right,
.SA_section--light .SA_caption-center,
.SA_section--light .SA_caption-left-flush {
  color: rgba(0, 0, 0, 0.76);
}

.SA_section--light .SA_pullquote {
  border: none;
  color: rgba(0, 0, 0, 0.92);
}

.SA_section--light hr {
  border-color: #eae7e0;
}

.SA_section--light .SA_related-article p {
  color: #0065a4;
}

.SA_section--light .SA_related-article__heading {
  border-bottom: 2px solid #e0dfdd;
}

.SA_section--dark {
  background: #080808;
  color: rgba(239, 239, 239, 0.99);
}

.SA_section--dark .SA_subtitle {
  color: white;
}

.SA_section--dark .SA_caption-right,
.SA_section--dark .SA_caption-center,
.SA_section--dark .SA_caption-left-flush {
  color: #929292;
}

.SA_section--dark hr {
  border-color: rgba(255, 255, 255, 0.04);
}

.SA_section--dark .SA_related-article__heading {
  border-bottom: 2px solid rgba(255, 255, 255, 0.04);
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

figure {
  padding: 0;
  margin: 0;
}

.SA_image-container {
  margin: 2rem auto 2rem auto;
}

.SA_image-container--no-bg figure {
  background: transparent;
}

.SA_image-container img {
  display: block;
  max-width: 100%;
  width: 100%;
}

.SA_after-scrollytelling-fix {
  background: #faf9f7;
  z-index: 50 !important;
  position: relative;
  backface-visibility: hidden;
}

.SA_scrollytelling-wrapper {
  margin-top: 63px;
  position: relative;
  overflow: visible;
}

.SA_scrollytelling-wrapper:after {
  content: "";
  padding-bottom: 72vh;
  display: block;
}

@media (min-width: 701px) {
  .SA_scrollytelling-wrapper:after {
    padding-bottom: 60vh;
  }
}

.SA_scrollytelling-wrapper--curtain-at-end {
  margin-bottom: 0;
  padding-bottom: 100vh;
}

.SA_curtain-active .SA_scroll-curtain {
  will-change: transform;
}

#SA_scroll-cta {
  z-index: 999999;
  display: flex;
  justify-content: center;
  opacity: 0;
  height: 34px;
}

#SA_scroll-cta div {
  padding: 1px 10px 2px 10px;
  border-radius: 500px;
  background: #0000008f;
  color: #fff;
  font-family: Inter, sans-serif;
  font-size: 14px;
  height: 22px;
  height: fit-content;
}

.SA_full-width-image,
.SA_full-width-image img {
  width: 100%;
}

.SA_scrollytelling-slide {
  position: relative;
  right: 0;
  backface-visibility: hidden;
}

.SA_scrollytelling-slide:nth-of-type(2) {
  margin-top: -40vh;
}

.SA_scrollytelling-slide .SA_scroll-text-wrapper {
  padding: 120vh 0 20vh 0;
  padding: 100vh 0 0vh;
  padding: 50vh 0;
  margin-bottom: -1px;
  position: relative;
  z-index: 2;
  transform: translate3D(0, 0, 0);
}

.SA_scrollytelling-slide .SA_scroll-text-wrapper * {
  color: rgba(255, 255, 255, 0.93);
}

.SA_scrollytelling-slide .SA_scroll-ad-wrapper {
  padding: 130vh 0 20vh 0;
  position: relative;
  z-index: 2;
  background: transparent;
}

.SA_scrollytelling-slide .SA_scroll-ad-wrapper .SA_advertisement {
  background: transparent;
}

.SA_scrollytelling-slide .SA_scroll-ad-wrapper .SA_advertisement__title {
  color: rgba(255, 255, 255, 0.93);
}

.SA_scrollytelling-slide:nth-of-type(2) .SA_scroll-text-wrapper {
  padding-top: 50vh;
}

.SA_scroll-curtain-wrapper {
  height: 50vh;
}

.SA_scroll-curtain {
  z-index: 1000;
  height: 300%;
  width: 50vw;
  background: #faf9f7;
  position: absolute;
  left: 0;
  backface-visibility: hidden;
}

.SA_scroll-curtain-right {
  left: auto;
  right: 0;
}

.SA_curtain-open {
  margin-top: -34px;
}

.SA_curtain-close .SA_scroll-curtain-left {
  transform: translateX(-200%);
}

.SA_curtain-close .SA_scroll-curtain-right {
  transform: translateX(200%);
}

.SA_scroll-text {
  border-radius: 4px;
  margin: 0 1rem;
  padding: 1rem 1.2rem;
  max-width: 500px;
  background: rgba(0, 0, 0, 0.75);
}

@media (min-width: 701px) {
  .SA_scroll-text {
    position: absolute;
    transform: translate3D(-50%, -50%, 0px);
    top: 50%;
    left: 50%;
    padding: 1.2rem 2rem;
    margin: 0 auto;
  }
}

@media (min-width: 1025px) {
  .SA_scroll-text--right {
    margin-right: 10%;
  }
  .SA_scroll-text--left {
    margin-left: 10%;
  }
}

@media (min-width: 1025px) {
  .SA_scroll-text-wrapper-right {
    margin: 0 auto;
    margin-left: 50%;
  }
  .SA_scroll-text-wrapper-left {
    margin: 0 auto;
    margin-right: 50%;
  }
}

.SA_scroll-text p {
  margin: 0;
}

.SA_scroll-text .SA_pullquote {
  margin: 0;
  padding: 0;
  border: none;
  width: auto;
  float: none;
  text-align: center;
  font-style: initial;
}

.SA_scrollytelling-images {
  position: sticky;
  top: 0;
  bottom: 0;
  z-index: 1;
  height: calc(100vh);
}

.SA_hide-nav .SA_scrollytelling-images {
  top: 0px !important;
}

.SA_fullscreen-image {
  backface-visibility: hidden;
  transform: translate3d(0, 0, 0);
  margin: 0;
  padding: 0;
  position: relative;
  opacity: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  position: absolute;
  transition: opacity 0.5s;
  transition-delay: 0.2s;
}

.SA_halfscreen-image {
  backface-visibility: hidden;
  transform: translate3d(0, 0, 0);
  margin: 0;
  padding: 0;
  position: relative;
  opacity: 0;
  z-index: 1;
  width: 50% !important;
  height: calc(100vh + 50px);
  position: absolute;
  transition: opacity 0.5s;
}

.SA_fullscreen-image__credit {
  position: absolute;
  bottom: 0;
  right: 10px;
  color: #ffffff;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 0.8rem;
  white-space: nowrap;
  font-family: "Inter", sans-serif;
}

.SA_scrollytelling-wrapper--scroll-in .SA_fullscreen-image:first-child {
  opacity: 1;
}

.SA_scrollytelling-wrapper--side-by-side {
  margin: 0 auto;
  position: relative;
}

@media (min-width: 1025px) {
  .SA_scrollytelling-wrapper--side-by-side .SA_scroll-text {
    width: 100%;
  }
  .SA_scrollytelling-wrapper--side-by-side .SA_fullscreen-image {
    width: 50% !important;
  }
  .SA_pullquote--image {
    width: calc((100% * 12/14) - 25px);
    margin-left: calc((-100% * 5/14) - 25px);
  }
}

.SA_fullscreen-image.SA_active-slide {
  opacity: 1;
  will-change: opacity;
  transition-delay: 0s;
}

.SA_fullscreen-image img {
  top: 0;
  left: 10%;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center bottom;
  margin: auto;
}

.SA_halfscreen-image img {
  top: 0;
  left: 10%;
  width: 50%;
  height: 100%;
  max-width: 1000px;
  object-fit: cover;
  object-position: center bottom;
  margin: auto;
}

.SA_fullscreen-image--contain img,
.SA_fullscreen-image--contain video {
  object-fit: contain;
  object-position: center;
  width: 100%;
  max-height: 100%;
}

.SA_fullscreen-video {
  position: absolute;
  right: -200%;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  z-index: -1;
  opacity: 0.5;
}

@media (min-width: 1025px) {
  .SA_fullscreen-video {
    right: 0;
    opacity: 0.5;
  }
}

.SA_fullscreen-video__overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
  background-image: radial-gradient(rgba(1, 3, 6, 0) 10%, rgba(1, 3, 6, 0.39) 85%);
}

.SA_fadeIn {
  opacity: 0;
  transition: transform 0.3s cubic-bezier(0.2, 0, 0.2, 1), opacity 0.4s ease-in !important;
  backface-visibility: hidden;
}

.SA_slideInLeft {
  opacity: 0;
  transition: transform 0.3s cubic-bezier(0.2, 0, 0.2, 1), opacity 0.4s ease-in !important;
  backface-visibility: hidden;
  transform: translateX(-50px);
}

.SA_slideInRight {
  opacity: 0;
  transition: transform 0.3s cubic-bezier(0.2, 0, 0.2, 1), opacity 0.4s ease-in !important;
  backface-visibility: hidden;
  transform: translateX(50px);
}

.SA_blurIn {
  opacity: 0.3;
  filter: blur(15px);
  transition: transform 0.3s cubic-bezier(0.2, 0, 0.2, 1), opacity 0.8s ease-in, filter 0.4s ease-in !important;
  backface-visibility: hidden;
  transform: translateY(50px) scale(1.2);
}

.SA_grow {
  width: 100%;
  transition: width 1s;
}

.SA_blurIn.SA_active {
  filter: blur(0px);
  opacity: 1 !important;
  transform: translateY(0px);
}

.SA_fadeIn.SA_active {
  opacity: 1 !important;
}

.SA_slideInLeft.SA_active,
.SA_slideInRight.SA_active {
  opacity: 1 !important;
  transform: translateX(0);
}

