}

.SA_arrow-button {
  width: 37px;
  height: 37px;
  background: none;
  border: 2px solid rgba(255, 255, 255, 0.84);
  border-radius: 50%;
  position: relative;
  cursor: pointer;
}

.SA_arrow-button:hover {
  background: rgba(255, 255, 255, 0.11);
  border-color: #fff;
}

.SA_arrow-button:hover svg path {
  fill: #fff !important;
}

.SA_arrow-button svg {
  width: 47%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.SA_arrow-button:focus {
  outline: none;
}

.SA_arrow-button:focus-visible::after {
  content: "";
  width: calc(100% + 14px);
  height: calc(100% + 14px);
  position: absolute;
  border: 2px solid #ffb600;
  top: -7px;
  left: -7px;
  border-radius: 50%;
}

.SA_arrow-button--left {
  transform: scale(-1);
}

.SA_arrow-button:disabled,
.SA_arrow-button[disabled] {
  cursor: not-allowed;
  border-color: #6b6b6b;
}

.SA_arrow-button:disabled svg path,
.SA_arrow-button[disabled] svg path {
  fill: #6b6b6b !important;
}

.SA_arrow-button:disabled:hover,
.SA_arrow-button[disabled]:hover {
  background: none;
}

