#SA_main-section {
    position: relative;
    height: 100vh;
    width: 100%;
}

#map {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
}

.mapboxgl-ctrl-attrib a {
    color: rgba(0, 0, 0, .75) !important;
    text-decoration: none;
}

#map-overlay {
    position: absolute;
    // bottom: 33px;
    // right: 15px;


    bottom: 55px;
    right: 0px;
    padding: 0 15px;

    display: flex;
    flex-direction: column-reverse;
    justify-content: end;
    flex-wrap: wrap;
    gap: 15px;

    @include tablet {
        flex-direction: column;
        padding: 0;
        max-width: 377px;
        right: 25px;
    }

    .SA_stepper-nav-text {
        // background: rgba(24, 24, 24, 0.75);
        // background: rgb(31, 31, 31);
        // background: #2c2c2c8c;
        border: 2px solid hsla(0, 0%, 100%, .84);
        padding: 25px 15px 25px 25px;
        border-radius: 4px;
        // @include text-stroke(2, #1f1f1f);
        // background: #1f1f1f9c;
        backdrop-filter: blur(2px);
    }

    p:last-child {
        margin-bottom: 0;
    }
}

.SA_h1,
.SA_h2,
.SA_h3,
.SA_h4,
    {
    font-weight: 400;
    letter-spacing: 0em;
    margin-top: 0;
}

#SA_stepper-nav {
    width: auto;
    display: flex;
    justify-content: end;
    gap: 15px;
    align-items: center;
}

.SA_nav-title {
    color: #fff;
    font-size: 1em;
    letter-spacing: 0.07em;
    margin: 0;
    font-weight: 700;
}

.SA_arrow-button {
    width: 37px;
    height: 37px;
    background: none;
    border: 2px solid hsla(0, 0%, 100%, .84);
    border-radius: 50%;
    position: relative;
    cursor: pointer;

    &:hover {
        background: rgba(255, 255, 255, 0.11);

        border-color: #fff;

        svg path {
            fill: #fff !important;
        }
    }

    svg {
        width: 47%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

.SA_arrow-button:focus {
    outline: none;
}

.SA_arrow-button:focus-visible::after {
    content: "";
    width: calc(100% + 14px);
    height: calc(100% + 14px);
    position: absolute;
    border: 2px solid #ffb600;
    top: -7px;
    left: -7px;
    border-radius: 50%;
}

.SA_arrow-button--left {
    transform: scale(-1);
}


.SA_arrow-button:disabled,
.SA_arrow-button[disabled] {
    // opacity: 0.4;
    cursor: not-allowed;
    border-color: #6b6b6b;

    svg path {
        fill: #6b6b6b !important;
    }

    &:hover {
        background: none;
    }
}