#SA_nav {
    margin: 0 auto;
    position: fixed;
    width: calc(100% - 16px);
    height: 32px;
    display: flex;
    z-index: 13;
    background: $bodyBackground;
    padding: 8px;
    transition: transform 0.4s;
    transform: translateY(-100px);
    // border-bottom: 1px solid rgba(255, 255, 255, 0.04);
    top: 0;
    transition-delay: 0.1s;
    // box-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
    border-bottom: 1px solid #{$borderColour};

    @include tablet {
        // height: 42px;
    }

}

#SA_currentChapter {
    font-family: #{$displayFont};
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    font-weight: bold;
    display: none;

    @include tablet {
        display: block;
    }
}


#SA_nav.SA_show,
#SA_nav:focus-within {
    transform: translateY(0px);
}

.SA_hide-nav #SA_nav {
    transform: translateY(-100px);
}

.SA_star-logo {
    width: 182px;
    padding: 17px 0 0;
    display: block;
    margin: 0 auto 14px;
}

.SA_undeniable-logo {
    font-size: 1rem;
    width: 300px;
    margin-bottom: 0rem;

    @include desktop {
        width: 450px;
        margin-bottom: -2rem;
        height: 252px;
    }
}

.SA_undeniable-logo-header {
    display: none;
    font-weight: bold;
    text-decoration: none;
    color: rgb(20, 20, 20);

    @include desktop {
        display: block;
        z-index: 10;
        top: 10px;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
    }
}

